.portfolio {
    margin: 0px 40px 40px 40px;
    max-width: 100vw;
    padding-top: 70px;
    transition: 1.5s;
  }
  
  .portfolio__header2 {
    margin: 0;
    padding: 0;
    font-size: 25px;
    margin-top: 10px;
    font-family: Play, Arial, Helvetica, sans-serif;
  }
      
  .portfolio__header3 {
    margin: 0;
    padding-left: 50px;
    font-size: 20px;
    font-family: Play, Arial, Helvetica, sans-serif;
    position: relative;
  }

  .portfolio__header5 {
    font-family: Play, Arial, Helvetica, sans-serif;
  }
  
  .portfolio__header3:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: -10%;
    left: 0;
    width: 36px;
    height: 36px;
    border-radius: 100%;
    background-image: linear-gradient(135deg, wheat 0,#fecd00 100%);
  }

  .portfolio__items {
    display: grid;
    list-style-type: none;
    grid-template-columns: auto auto auto;
    justify-items: center;
    padding: 40px 0 0 0;
    column-gap: 10px;
    row-gap: 30px;
  }

  .portfolio__item {
    cursor: pointer;
    border: #fecd00 3px solid;
    padding: 5px;
  }

  .portfolio__item-fade:nth-child(n+4) {
    animation: fade 1s ease forwards;
  }

  .portfolio__item-fade-in:nth-child(n+4) {
    animation: fade-in 2.5s ease-in-out;
  }

  .portfolio__icon_git {
    width: 26px;
  }

  @keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
  }

  @keyframes fade {
    0% {
      opacity: 1;
    }
    to {
      opacity: 0;
    }
  }

  .portfolio__img {
    max-width: 280px;
    max-height: 202px;
  }

  .portfolio__links {
    display: flex;
    flex-direction: row;
    padding-top: 8px;
    justify-content: space-evenly;
    align-items: center;
  }

  .portfolio__link:hover {
    opacity: 0.3;
    transition: 0.8s;
  }

  .portfolio__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .portfolio__button {
    margin: 40px 0 0 0;
    padding: 0;
    width: 180px;
    height: 50px;
    border: none;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: center;
    border-radius: 10px;
    background-image: linear-gradient(135deg, wheat 0,#fecd00 100%);
    color: black;
    cursor: pointer;
    animation: fade-in 0.7s ease-in-out;
    font-family: Play, Arial, Helvetica, sans-serif;
}

.portfolio__button:hover {
  opacity: 0.4;
  transition: 0.8s;
}

  @media screen and (min-width: 679px) and (max-width: 999px){
    .portfolio__items {
        grid-template-columns: auto auto;
        margin: auto;
    }
  }

  @media screen and (min-width: 320px) and (max-width: 679px){
    .portfolio {
      margin: 0;
    }
    
    .portfolio__items {
        grid-template-columns: auto;
        margin: auto;
    }
  }

  @media screen and (min-width: 320px) and (max-width: 900px){
  
    .portfolio__headers{
      padding: 0 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
  }

  .portfolio__header2 {
    text-align: center;
  }
    
  .portfolio__header3 {
    text-align: center;
    padding-left: 0;
  }

  .portfolio__header3::before {
    left: -41%;
  }
}
